
/**
 * vuex管理登陆状态，具体可以参考官方登陆模板示例
 */
// import {
// 	mapMutations
// } from 'vuex';

export default {
	globalData: {
		hasLogin: false
	},
	methods: {
		login(data) {
			uni.request({
				url: this.myroot + "login",
				data: data,
				dataType: "json",
				method: "POST",
				success: (res) => {
					console.log(res);
					getApp().globalData.hasLogin = true;
					uni.showToast({
						title: "登陆成功",
						icon: "success"
					})
				
				}
			})
		}
		//...mapMutations(['login'])
	},
	onLaunch: function() {
		let userInfo = uni.getStorageSync('userInfo') || '';
		if (userInfo.id) {
			//更新登陆状态
			uni.getStorage({
				key: 'userInfo',
				success: (res) => {
					this.login(res.data);
				}
			});
		}

	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	},
}
